import { h } from 'preact'
import styled, { css } from 'styled-components'

const media = {
    mobile: (...args) => css`
        @media (max-width: 400px) {
            ${css(...args)};
        }
    `,
}

const Approach = styled.section`
    height: 40em;
    background-color: white;
`
const Container = styled.div`
    width: 70%;
    margin: auto;
    padding-top: 8em;
`

const Parent = styled.div`
    display: flex;
`

const FlexChild = styled.div`
    ${({ flex }) => flex && `flex: ${flex};`};
`

export const Heading = styled.h1`
    color: black;
    font-size: 1.4em;
    text-align: center;
    letter-spacing: 0.15em;
    width: 85%;
    margin: auto;
    font-weight: 500;
    text-align: left;
    line-height: 2em;
    font-style: italic;

    ${media.mobile`
      padding-top: 6em; 
      font-size: 1.4em;
      width: 70%;
    `};
`

export default () => (
    <Approach>
        <Container>
            <Parent>
                <FlexChild flex="2 0 0">
                    <Heading>
                        We are a full-service agency that is passionate about digital products. We
                        provide elegant solutions to difficult technical problems, build engaging
                        customer experiences and help clients reach business goals.
                    </Heading>
                </FlexChild>
                <FlexChild flex="1 0 0">2</FlexChild>
            </Parent>
        </Container>
    </Approach>
)



